* ===================================================
*/
.ribbon {
  position: absolute;
  top: 10px;
  left: -11px;
  z-index: 999;
  display: inline-block;
  font-family: "HK Grotesk", sans-serif;
  font-size: .8rem;
  text-transform: uppercase;
  letter-spacing: .3em;
  padding: 3px 15px 5px;
  color: #fff;
