}

.ribbon + .ribbon {
  top: 60px;
}

.ribbon + .ribbon + .ribbon {
  top: 110px;
}

.ribbon + .ribbon + .ribbon + .ribbon {
  top: 160px;
}

.ribbon::before {
  content: '';
  border-left: 12px solid transparent;
  border-right: 0 solid transparent;
  border-top: 14px solid #224c23;
  position: absolute;
  bottom: -14px;
  left: 0;
}

