}

.ribbon-danger::before {
  border-top-color: #921925;
}

.ribbon-light {
  background-color: #f8f9fa !important;
}

.ribbon-light::before {
  border-top-color: #bdc6d0;
}

.ribbon-dark {
  background-color: #343a40 !important;
}

.ribbon-dark::before {
  border-top-color: #060708;
}

.ribbon-gray-100 {
